import { useTranslation } from "react-i18next";
import style from "../FinanceManager/StudentStatitics/StudentStatitics.module.css";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useCallback } from "react";
const StudentEnrollmentTable = ({ tableData, parentUrl }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();
  const getQuery = useLocation();

  const onNavigate = useCallback(
    (val) => {
      if (val?.length > 0) {
        let url = parentUrl
          ? parentUrl
          : getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/admission?type=statistics-detail`
          : `/q/${params.username}/member/staff/admission?type=statistics-detail`;
        navigate(url, {
          state: {
            ...getQuery.state,
            studentList: val,
          },
        });
      }
    },
    [navigate, params, getQuery.state]
  );

  return (
    <>
      <section
        //    className={style.set_section}
        style={{
          width: "100%",
        }}
      >
        {tableData?.map((data, index) => (
          <div
            className={style.set_section}
            key={`${data?.DepartmentName}${index}`}
          >
            <div className={style.sets_c}>
              <h6>
                {data?.DepartmentName ?? ""} - {data?.Batch ?? ""}
              </h6>
              <div className={style.setsc_n}>
                {data?.excel_list?.map((d_data, dindex) => (
                  <h6
                    className={style.setscr_f}
                    key={`${d_data?.StandardName}${dindex}`}
                  >
                    {d_data?.StandardName ?? ""}
                  </h6>
                ))}
              </div>
            </div>

            <div className={style.sets_c}>
              <h6>Regular</h6>
              <div className={style.setsc_r}>
                <div className={style.setscr_f}>
                  <h6>Male</h6>
                </div>
                <div className={style.setscr_f}>
                  <h6>Female</h6>
                </div>
                <div className={style.setscr_f}>
                  <h6>Total</h6>
                </div>
              </div>
            </div>
            <div className={style.sets_c}>
              <h6>DSE</h6>
              <div className={style.setsc_r}>
                <div className={style.setscr_f}>
                  <h6>Male</h6>
                </div>
                <div className={style.setscr_f}>
                  <h6>Female</h6>
                </div>
                <div className={style.setscr_f}>
                  <h6>Total</h6>
                </div>
              </div>
            </div>
            <div className={style.sets_c}>
              <h6>Total</h6>
            </div>
          </div>
        ))}
      </section>
    </>
  );
};

export default StudentEnrollmentTable;
